@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;500;800&display=swap);
:root {
  --background: #12192F;
  --blue: #517DFF;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  /* background-color: var(--background); */
}
.home {
  min-height: 100vh;
  position: relative;
}

.landing {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.landing > p {
  margin: 15px;
}

.profile > header {
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 0;
}

.profile > header .myid {
  font-size: 3rem;
  color: var(--blue);
  cursor: pointer;
  display: block;
  margin: 10px 0;
}

.profile > header .myid span {
  line-height: 3rem;
}

.alert-text {
  opacity: .5;
}

.connections header {
  padding: 0 20px;
}

.connections header p {
  font-size: 1.2rem;
}

.makeconnection {
  padding: 20px;
  margin: 10px 20px;
  border-radius: 10px;
  background-color: var(--blue);
  display: flex;
  flex-direction: column;
}

.add-connection {
  position: absolute;
  bottom: 20px;
  left: 20px;
}
/*# sourceMappingURL=home.css.map */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

